@charset 'UTF-8';
/* Slider */
.slick-loading .slick-list
{
    background: #fff url('./ajax-loader.gif') center center no-repeat;
}
.mySlides {
    display: none; 
    margin-left: auto;
    margin-right: auto;
    max-width: 50%;
  }
  
  /* Slideshow container */
  .slideshow-container {
    max-width: 100%;
    position: relative;
    margin: auto;
    cursor: grab;
    margin-bottom: 2.5rem;
  }
  .slideshow-container:active {
    cursor: grabbing;
  }
  
  /* Fading animation */
  .fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s; }
  
  @-webkit-keyframes fade {
    from {
      opacity: .4; }
    to {
      opacity: 1; } }
  @keyframes fade {
    from {
      opacity: .4; }
    to {
      opacity: 1; } }
  /* On smaller screens, decrease text size */
  @media only screen and (max-width: 300px) {
    .prev, .next, .text {
      font-size: 11px; } }
  
  
      /* Slider */
      .slick-slider
      {
          position: relative;
  
          display: block;
          box-sizing: border-box;
  
          -webkit-user-select: none;
             -moz-user-select: none;
              -ms-user-select: none;
                  user-select: none;
  
          -webkit-touch-callout: none;
          -khtml-user-select: none;
          -ms-touch-action: pan-y;
              touch-action: pan-y;
          -webkit-tap-highlight-color: transparent;
      }
  
      .slick-list
      {
          position: relative;
  
          display: block;
          overflow: hidden;
  
          margin: 0;
          padding: 0;
      }
      .slick-list:focus
      {
          outline: none;
      }
      .slick-list.dragging
      {
          cursor: grab;
          cursor: hand;
      }
  
      .slick-slider .slick-track,
      .slick-slider .slick-list
      {
          -webkit-transform: translate3d(0, 0, 0);
             -moz-transform: translate3d(0, 0, 0);
              -ms-transform: translate3d(0, 0, 0);
               -o-transform: translate3d(0, 0, 0);
                  transform: translate3d(0, 0, 0);
      }
  
      .slick-track
      {
          position: relative;
          top: 0;
          left: 0;
  
          display: block;
          margin-left: auto;
          margin-right: auto;
      }
      .slick-track:before,
      .slick-track:after
      {
          display: table;
  
          content: '';
      }
      .slick-track:after
      {
          clear: both;
      }
      .slick-loading .slick-track
      {
          visibility: hidden;
      }
  
      .slick-slide
      {
          display: none;
          float: left;
  
          height: 100%;
          min-height: 1px;
      }
      [dir='rtl'] .slick-slide
      {
          float: right;
      }
      .slick-slide img
      {
          display: block;
      }
      .slick-slide.slick-loading img
      {
          display: none;
      }
      .slick-slide.dragging img
      {
          pointer-events: none;
      }
      .slick-initialized .slick-slide
      {
          display: block;
      }
      .slick-loading .slick-slide
      {
          visibility: hidden;
      }
      .slick-vertical .slick-slide
      {
          display: block;
  
          height: auto;
  
          border: 1px solid transparent;
      }
      .slick-arrow.slick-hidden {
          display: none;
      }
/* Arrows */
.slick-prev,
.slick-next
{
    font-family: 'Akrobat-Black', sans-serif;
    font-size: 0;
    line-height: 0;

    position: absolute;
    top: 50%;
    z-index: 1;

    display: block;

    width: 50px;
    height: 100%;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);

    cursor: pointer;

    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus
{
    color: black;
    outline: none;
    background: transparent;
}
.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before
{
    opacity: 1;
}
.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before
{
    opacity: .25;
}

.slick-prev:before,
.slick-next:before
{
    /* font-family: 'slick'; */
    font-family: 'Rubik', 'sans-serif';
    font-weight: 300;
    font-size: 48px;
    line-height: 1;

    opacity: 1;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transition: opacity .4s cubic-bezier(.59,.01,.28,1);
    -o-transition: opacity .4s cubic-bezier(.59,.01,.28,1);
    transition: opacity .4s cubic-bezier(.59,.01,.28,1);
}

.slick-prev
{
    text-align: start;
    left: 3%;
}
[dir='rtl'] .slick-prev
{
    right: 3%;
    left: auto;
}
.slick-prev:before
{
    content: '<';
    /* content: '←'; */
}
[dir='rtl'] .slick-prev:before
{
    content: '>';
    /* content: '→'; */
}

.slick-next
{
    text-align: end;
    right: 6%;
}
[dir='rtl'] .slick-next
{
    right: auto;
    left: 6%;
}
.slick-next:before
{
    content: '>';
    /* content: '→'; */
}
[dir='rtl'] .slick-next:before
{
    content: '>';
    /* content: '→'; */
}

/* Dots */
.slick-dotted.slick-slider
{
    margin-bottom: 30px;
}

.slick-dots
{
    position: absolute;
    bottom: 21px;

    display: block;

    width: 100%;
    padding: 0;
    margin: 0;

    list-style: none;

    text-align: center;
}
.slick-dots li
{
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button
{
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 5px;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus
{
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before
{
    opacity: 1;
}
.slick-dots li button:before
{
    font-family: 'slick';
    font-size: 6px;
    line-height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    content: '•';
    text-align: center;

    opacity: .25;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before
{
    opacity: .75;
    color: black;
}
